import Layout from '../components/MyLayout';
import {useSelector, useDispatch} from 'react-redux';

export default function Cart() {
	const this_cart = useSelector(state => state.cart.cart)
	let hash = {}
	return (
		<Layout>
		{this_cart.map(item => <div>{item.title}</div>)}
			<p>Cart</p>
		</Layout>
	);
}